import React, { useState } from "react";

const RadioButtonGroup = ({ options, name, className, onChange }) => {
  const [selectedOption, setSelectedOption] = useState("");

  const handleChange = (e) => {
    const { value } = e.target;
    setSelectedOption(value);
    if (onChange) {
      onChange(e); // Call the passed callback function
    }
  };
  const renderCheckList =()=>{
    return(
        options.map((option, index) => (
            <li key={index}>
              <div className="typeCheck">
                <input
                  type="radio"
                  id={`${name}-${index + 1}`}
                  name={name}
                  value={option?.value}
                  checked={selectedOption === option?.value} // Set checked based on the selected option
                  className="d-none"
                  onChange={handleChange} // Trigger handleChange on radio button change
                />
                <label htmlFor={`${name}-${index + 1}`}>
                  {option?.label} <i className="bi bi-check-lg" />
                </label>
              </div>
            </li>
          ))
    )
  }

  return (
    <ul className={className}>
      {renderCheckList()}
    </ul>
  );
};

export default RadioButtonGroup;
