import React, { useState } from 'react'
import { CustomTabs } from '../../../reuseableComponents'
import ProbationRecord from './ProbationRecord';
import ManagerComments from './ManagerComments';
const selectedDatas = {
    employeeId: "QD1024",
    employeeName: "Nikita Prajapat",
    jobTitle: "Junior Web Designer",
    department: "Product Development",
    dateOfJoining: "01 September 2023",
    reportingManager: "vinod.vijay@qdegrees.com",
    threeMonthReview: "06 January 2024",
    sixMonthReview: "01 March 2024"
}
const CreateViewProbationReviewForm = (props) => {
    const { openModel, handleOpenClose, } = props
    const [errors, setErrors] = useState('')
    const [selectedData,setSelectedData]=useState(selectedDatas)
    const [creteViewProRecordForm, setCreteViewProRecordForm] = useState({
        employeeId: "QD1024",
        employeeName: "Nikita Prajapat",
        jobTitle: "Junior Web Designer",
        department: "Product Development",
        dateOfJoining: "01-05-2023",
        reportingManager: "vinod.vijay@qdegrees.com",
        threeMonthReview: "06-11 2024",
        sixMonthReview: "01-12-2024",
        sixMonthReview:'',
        "qualityAccuracyWork":'',
        "efficiency":"",
        "attendance":"",
        "Time Keeping":"",
        "workRelationship":'',
        "competencyRole":'',
        "probationaryPeriodBeenMeet":'',
        "probationaryPeriodBeenAddressed":'',
        "employeePerformance":'',
        "completedProbationary":'',
        "probationaryExtended":''

    })

    const tableTabsRequest = [
        { name: "Probation Record", onClickFun: (id) => probationData(id), id: "probationRecord" },
        { name: "Manager’s comments", onClickFun: (id) => probationData(id), id: "managerComments" },
    ];
    const probationData = () => {

    }
    const handleFormChange = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e?.target ? e.target : { value: e };

        setCreteViewProRecordForm(prevState => {
            const updatedState = {
                ...prevState,
                [name]: type === 'checkbox' ? checked : value,
            };
            return updatedState;
        });

        // Reset error for this field
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));
    };

    return (
        <>
            <div className={`offcanvas offcanvas-end  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                    width: "800px"
                }}
                id="interviewDetail"
            >

                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">
                        Managers Probation Review Form
                    </h5>
                    <button
                        type="button"
                        className="btn-close"
                        data-bs-dismiss="offcanvas"
                        aria-label="Close"
                        onClick={() => handleOpenClose()}
                    />
                </div>
                <div className="offcanvas-body">
                    <dixv className="probationForm">
                        <h3>Please Note:</h3>
                        <p>
                            Managers are required to submit a copy of this form to HR even if the
                            employee’s performance is satisfactory. Failure to complete and submit
                            this form will result in the assumption that the employee’s probation
                            period has progressed satisfactorily.
                        </p>
                        <div className="probationContent">
                            <CustomTabs tabs={tableTabsRequest} type="pmsAdmin" />

                            <div className="tab-content">
                                <div className="tab-pane fade show active" id="probationRecord">
                                    <ProbationRecord
                                        creteViewProRecordForm={creteViewProRecordForm}
                                        setCreteViewProRecordForm={setCreteViewProRecordForm}
                                        handleFormChange={handleFormChange}
                                    />
                                </div>
                                <div className="tab-pane fade" id="managerComments">
                                    <ManagerComments
                                        creteViewProRecordForm={creteViewProRecordForm}
                                        setCreteViewProRecordForm={setCreteViewProRecordForm}
                                        handleFormChange={handleFormChange}

                                    />
                                </div>
                            </div>
                        </div>
                        <div className="d-flex align-items-center justify-content-end gap-2 border-top pt-3">
                            <button
                                type="button"
                                className="btn btn-org-gradient"
                                data-bs-toggle="modal"
                                data-bs-target="#otpModal"
                            >
                                Verify
                            </button>
                            <button type="button" className="btn btn-secondary">
                                Back
                            </button>
                        </div>
                    </dixv>


                </div>
            </div >
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default CreateViewProbationReviewForm

