import React, { useMemo } from 'react'
import { CustomTable } from '../../../reuseableComponents';
import { setStatusColor } from '../../../../utils/common';
import Image from '../../../commanComponent/Image';

const ProbationReviewTable = (props) => {
    const { currentPage, setCurrentPage, probationReviewData, toggleCreateViewProbationForm } = props

    const probationReviewField = [
        { key: 'sn', label: 'S.N' },
        { key: 'empId', label: 'Emp Id' },
        { key: 'empName', label: 'Emp Name' },
        { key: 'joiningDate', label: 'Joining Date' },
        { key: 'probationEndDate', label: 'Probation End Date' },
        { key: 'probationExtended', label: 'probationExtended' },
        { key: 'mobileNo', label: 'Mobile No' },
        { key: 'status', label: 'Account Status' },
        { key: 'action', label: 'Action' }
    ];
    const probationReviewScopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),
        status: (item, index) => (
            <span
                className={`${setStatusColor(item?.status)}`}
            >
                {item?.status}
            </span>
        ),

        action: item => (
            <span>
                {item?.status.toLowerCase() === "pending" && <span
                onClick={()=>toggleCreateViewProbationForm()}
                >
                    <Image
                        imgSrc={"images/editbg-Icon.svg"}
                        width={"32"}
                        tooltipText={"Fill Form"}
                    />
                </span>}
                {item?.status.toLowerCase() === "completed" && <span
                onClick={()=>toggleCreateViewProbationForm()}
                >
                    <Image
                        imgSrc={"images/view-icon.svg"}
                        width={"32"} tooltipText={"View"}
                    />
                </span>}
            </span>
        )
    }), [probationReviewData]);
    return (
        <div>
            <div className="table-responsive tableDiv text-nowrap">
                <CustomTable
                    data={probationReviewData || []}
                    fields={probationReviewField}
                    // isLoading={isLoading}
                    pagination={true}
                    currentPage={currentPage}
                    setCurrentPage={setCurrentPage}
                    totalPages={probationReviewData?.totalPages}
                    tableName={"Probation Reviews Status"}
                    // isHeader={false}
                    // tableStyle={{ padding: "0px", boxShadow: "none" }}
                    scopedSlots={probationReviewScopedSlots}
                />
            </div>
        </div>
    )
}

export default ProbationReviewTable
