import React from 'react'
import { useCustomTranslation } from '../../../../translation'
import { CustomStrong, Textarea } from '../../../commanComponent'
import RadioButtonGroup from '../../../commanComponent/RadioButtonGroup'
import { qualityOptions, yesNoOptions } from '../../../../utils/Constants.config'

const ManagerComments = (props) => {
    const { creteViewProRecordForm, handleFormChange } = props
    const { t } = useCustomTranslation()
   
    return (
        <div>
            <p>
                This section should be completed by the manager before two weeks
                of the employee completing his/her probation.
            </p>
            <div className=" reviewsComment align-items-center p-0">
                <div className="form-group mb-3">
                    <CustomStrong text={t("labels.sixMonthReview")} />
                    <Textarea
                        value={creteViewProRecordForm?.sixMonthReview}
                        placeholder={t("placeholders.sixMonthReview")}
                        onChange={(e) => handleFormChange(e)}
                        name={"sixMonthReview"}
                    />

                </div>
                <div className="form-group mb-3">
                    <CustomStrong text={t("labels.qualityAccuracyWork")} />
                    <p>
                        Lorem Ipsum is simply dummy text of the printing and
                        typesetting industry.
                    </p>
                    <RadioButtonGroup
                        options={qualityOptions}
                        name="qualityAccuracyWork"
                        className="typeCheckList"
                        onChange={handleFormChange}
                    />

                </div>
                <div className="form-group mb-3">
                    <CustomStrong text={t("labels.efficiency")} />
                    <p>
                        Lorem Ipsum is simply dummy text of the printing and
                        typesetting industry.
                    </p>
                    <RadioButtonGroup
                        options={qualityOptions}
                        name="efficiency"
                        className="typeCheckList"
                        onChange={handleFormChange}
                    />
                </div>
                <div className="form-group mb-3">
                    <CustomStrong text={t("labels.attendance")} />
                    <p>
                        Lorem Ipsum is simply dummy text of the printing and
                        typesetting industry.
                    </p>
                    <RadioButtonGroup
                        options={qualityOptions}
                        name="attendance"
                        className="typeCheckList"
                        onChange={handleFormChange}
                    />
                </div>
                <div className="form-group mb-3">
                    <CustomStrong text={t("labels.timeKeeping")} />
                    <p>
                        Lorem Ipsum is simply dummy text of the printing and
                        typesetting industry.
                    </p>
                    <RadioButtonGroup
                        options={qualityOptions}
                        name="timeKeeping"
                        className="typeCheckList"
                        onChange={handleFormChange}
                    />
                </div>
                <div className="form-group mb-3">
                    <CustomStrong text={t("labels.workRelationship")} />

                    <p>
                        Lorem Ipsum is simply dummy text of the printing and
                        typesetting industry.
                    </p>
                    <RadioButtonGroup
                        options={qualityOptions}
                        name="workRelationship"
                        className="typeCheckList"
                        onChange={handleFormChange}
                    />
                </div>
                <div className="form-group mb-3">
                    <CustomStrong  text={t("labels.competencyRole")} />
                    <p>
                        rrrrrrr Lorem Ipsum is simply dummy text of the printing and
                        typesetting industry.
                    </p>
                    <RadioButtonGroup
                        options={yesNoOptions}
                        name="competencyRole"
                        className="typeCheckList"
                        handleChange={handleFormChange}
                        selectedValue={creteViewProRecordForm?.competencyRole}
                    />
                </div>
                <div className="form-group mb-3">
                    <CustomStrong text={t("labels.probationaryPeriodBeenMeet")} />

                    <p>
                        Lorem Ipsum is simply dummy text of the printing and
                        typesetting industry.
                    </p>
                    <RadioButtonGroup
                        options={yesNoOptions}
                        name="probationaryPeriodBeenMeet"
                        className="typeCheckList"
                        onChange={handleFormChange}
                    />
                </div>
                <div className="form-group mb-3">
                    <CustomStrong text={t("labels.probationaryPeriodBeenAddressed")} />

                    <p>
                        Lorem Ipsum is simply dummy text of the printing and
                        typesetting industry.
                    </p>
                    <RadioButtonGroup
                        options={yesNoOptions}
                        name="probationaryPeriodBeenAddressed"
                        className="typeCheckList"
                        onChange={handleFormChange}
                    />

                </div>
                <div className="form-group mb-3">
                    <CustomStrong text={t("labels.employeePerformance")} />
                    <Textarea
                        value={creteViewProRecordForm?.employeePerformance}
                        placeholder={t("placeholders.employeePerformance")}
                        onChange={(e) => handleFormChange(e)}
                        name={"employeePerformance"}
                    />

                </div>
                <div className="form-group mb-3">
                <CustomStrong text={t("labels.completedProbationary")} />
                   
                    <p>
                        Lorem Ipsum is simply dummy text of the printing and
                        typesetting industry.
                    </p>
                    <RadioButtonGroup
                        options={yesNoOptions}
                        name="completedProbationary"
                        className="typeCheckList"
                        onChange={handleFormChange}
                    />
                </div>
                <div className="form-group mb-3">
                    <CustomStrong text={t("labels.probationaryExtended")} />
                   
                    <p>
                        Lorem Ipsum is simply dummy text of the printing and
                        typesetting industry.
                    </p>
                    <RadioButtonGroup
                        options={yesNoOptions}
                        name="probationaryExtended"
                        className="typeCheckList"
                        onChange={handleFormChange}
                    />
                </div>
            </div>
        </div>
    )
}

export default ManagerComments
