import React from 'react'
import { Input, Label } from '../../../commanComponent'
import { useCustomTranslation } from '../../../../translation'
import moment from 'moment'

const ProbationRecord = (props) => {

    const { creteViewProRecordForm, setCreteViewProRecordForm, handleFormChange } = props
    const { t } = useCustomTranslation()
    return (
        <div className="formPanel">
            <div className="row">
                <div className="col-md-6">
                    <div className="form-group mb-3">
                        <Label
                            text={t("labels.employeeID")}
                        />
                        <Input
                            type="text"
                            name="employeeId"
                            value={creteViewProRecordForm?.employeeId}
                            onChange={(e) => handleFormChange(e)}
                        />

                    </div>
                </div>
                <div className="col-md-6">
                    <div className="form-group mb-3">
                        <Label
                            text={t("labels.employeeName")}
                        />
                        <Input
                            type="text"
                            name="employeeName"
                            value={creteViewProRecordForm?.employeeName}
                            onChange={(e) => handleFormChange(e)}
                        />

                    </div>
                </div>
                <div className="col-md-12">
                    <div className="form-group mb-3">
                        <Label
                            text={t("labels.JobTitle")}
                        />
                        <Input
                            type="text"
                            name="JobTitle"
                            placeholder={"JobTitle"}
                            value={creteViewProRecordForm?.JobTitle}
                            onChange={(e) => handleFormChange(e)}
                        />

                    </div>
                </div>
                <div className="col-md-12">
                    <div className="form-group mb-3">
                        <Label
                            text={t("labels.department")}
                        />
                        <Input
                            type="text"
                            name="department"
                            placeholder={"department"}
                            value={creteViewProRecordForm?.department}
                            onChange={(e) => handleFormChange(e)}
                        />

                    </div>
                </div>
                <div className="col-md-6">
                    <div className="form-group mb-3">
                        <Label
                            text={t("labels.dateOfJoining")}
                        />
                        <Input
                            type="date"
                            name="dateOfJoining"
                            placeholder={"dateOfJoining"}
                            value={moment(creteViewProRecordForm?.dateOfJoining).format("YYYY-DD-MM")}
                            onChange={(e) => handleFormChange(e)}
                        />

                    </div>
                </div>
                <div className="col-md-6">
                    <div className="form-group mb-3">
                        <Label
                            text={t("labels.reportingManager")}
                        />
                        <Input
                            type="text"
                            name="reportingManager"
                            placeholder={"reportingManager"}
                            value={creteViewProRecordForm?.reportingManager}
                            onChange={(e) => handleFormChange(e)}
                        />

                    </div>
                </div>
                <div className="col-md-6">
                    <div className="form-group mb-3">
                    <Label
                            text={t("labels.threeMonthReview")}
                        />
                        <Input
                            type="date"
                            name="threeMonthReview"
                            placeholder={"threeMonthReview"}
                            value={moment(creteViewProRecordForm?.threeMonthReview).format("YYYY-DD-MM")}
                            onChange={(e) => handleFormChange(e)}
                        />

                        
                    </div>
                </div>
                <div className="col-md-6">
                    <div className="form-group mb-3">
                    <Label
                            text={t("labels.sixMonthReview")}
                        />
                        <Input
                            type="date"
                            name="sixMonthReview"
                            placeholder={"sixMonthReview"}
                            value={moment(creteViewProRecordForm?.sixMonthReview).format("YYYY-DD-MM")}
                            onChange={(e) => handleFormChange(e)}
                        />
                        
                    </div>
                </div>
            </div>
        </div>
    )
}

export default ProbationRecord
